import React from 'react'
import { reduxForm } from 'redux-form'
import { maxLengthCreator, required } from '../../../utilits/validators/validators'
import { Input, CreateField } from '../../common/FormsControls/FormsControls'
import s from '../Login.module.css'

const maxLength40 = maxLengthCreator(40)

const LoginForm = ({ handleSubmit, error, captchaURL }) => {
    return (
        <form onSubmit={handleSubmit}>
            {CreateField('Email', 'email', [required, maxLength40], Input)}
            {CreateField('Password', 'password', [required, maxLength40], Input, { type: 'password' })}
            <div className={s.checkboxDiv}>
                {CreateField(null, 'rememberMe', [], Input, { type: 'checkbox' })}
                <span className={s.span}>remember me</span>
            </div>

            {captchaURL && <img src={captchaURL} alt={'CaptchaURL is undefined'}></img>}
            {captchaURL && CreateField('Symbols from image', 'captcha', [required], Input)}

            { error &&
                <div className={s.formSummaryError}>
                    {error}
                </div>}
            <div>
                <button className={s.submit} type={'submit'}>Login</button>
            </div>
        </form>
    )
}
const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)

export default LoginReduxForm