import { authAPI, securityAPI } from '../api/api'
import { stopSubmit } from 'redux-form'

const SET_USER_DATA = 'react-app/auth/SET-USER-DATA'
const TOGGLE_IS_FETCHING = 'react-app/auth/TOGGLE-IS-FETCHING'
const GET_CAPTCHA_URL_SUCCESS = 'react-app/auth/GET-CAPTCHA-URL-SUCCESS'

let initialState = {
    UserId: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    captchaURL: null
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                captchaURL: action.captchaURL
            }
        default:
            return state
    }
}

export const setAuthUserData = (UserId, email, login, isAuth) => ({ type: SET_USER_DATA, payload: { UserId, email, login, isAuth } })
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching })
export const getCaptchaURLSuccess = (captchaURL) => ({ type: GET_CAPTCHA_URL_SUCCESS, captchaURL }) //–£ –Ω–µ–≥–æ —Ç—É—Ç —Å—Ç–æ—è–ª payload (—á–µ–∫ –ø—Ä–∏ –¥–µ–±–∞–≥–µ)

export const getAuthUserData = () => async (dispatch) => {
    dispatch(toggleIsFetching(true))

    let response = await authAPI.me()

    dispatch(toggleIsFetching(false))

    if (response.resultCode === 0) {
        let { id, login, email } = response.data
        dispatch(setAuthUserData(id, email, login, true))
    }
}

export const login = (email, password, rememberMe, captcha) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe, captcha)

    if (response.data.resultCode === 0) {
        console.log(response)
        dispatch(getAuthUserData())
    } else {
        if (response.data.resultCode === 10) {
            dispatch(getCaptchaURL())
        }
        let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Common error'
        dispatch(stopSubmit('login', { _error: message }))
    }
}

export const logout = () => async (dispatch) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        console.log(response)
        dispatch(setAuthUserData(null, null, null, false))
    }
}

export const getCaptchaURL = () => async (dispatch) => {
    const response = await securityAPI.getCaptchaURL()
    const captchaURL = response.data.url
    dispatch(getCaptchaURLSuccess(captchaURL))
}

export default authReducer