import React, { useState } from 'react'
import s from './Paginator.module.css'
import cn from 'classnames'

let Paginator = ({ totalUsersCount, pageSize, currentPage, onPageChanged, portionSize }) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize)

    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize) //–≤—Å–µ–≥–æ —Å—Ç—Ä–∞–Ω–∏—Ü / —Ä–∞–∑–º–µ—Ä –ø–æ—Ä—Ü–∏–∏
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionPageNumber = portionNumber * portionSize

    return (
        <div className={s.paginator}>
            {portionNumber > 1 &&
                <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button>}

            {pages
                .filter(page => page >= leftPortionPageNumber && page <= rightPortionPageNumber)
                .map((page) => {
                    return (
                        <span className={cn({ [s.selectedPage]: currentPage === page }, s.pageNumber)} key={page} onClick={(event) => { onPageChanged(page) }}>{page}</span>
                    )
                })}
            {portionCount > portionNumber &&
                <button onClick={() => { setPortionNumber(portionNumber + 1) }} >NEXT</button>}
        </div>
    )
}

export default Paginator