import { getAuthUserData } from "./authReducer"

const INITIALIZED_SUCCESS = 'react-app/app/INITIALIZED-SUCCESS'
const SET_ERROR_STATUS = 'react-app/app/SET-ERROR-STATUS'
const NULL_ERROR_STATUS = 'react-app/app/NULL-ERROR-STATUS'

let initialState = {
    initialized: false,
    errorStatus: null
}

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case INITIALIZED_SUCCESS:
            return {
                ...state,
                initialized: true
            }
        case SET_ERROR_STATUS:
            return {
                ...state,
                errorStatus: action.errorStatus
            }
        case NULL_ERROR_STATUS:
            return {
                ...state,
                errorStatus: null
            }
        default:
            return state
    }
}

export const InitializedSuccess = () => ({ type: INITIALIZED_SUCCESS })
export const setErrorStatus = (errorStatus) => ({ type: SET_ERROR_STATUS, errorStatus })
export const nullErrorStatus = () => ({ type: NULL_ERROR_STATUS })

export const InitializeApp = () => (dispatch) => {
    let promise = dispatch(getAuthUserData())

    Promise.all([promise]).then(() => {
        dispatch(InitializedSuccess())
    })
}

export const showGlobalError = () => (dispatch) => {
    dispatch(setErrorStatus(true))
    setTimeout(() => {
        dispatch(nullErrorStatus)
    }, 1000)
    clearTimeout()
}

export default appReducer