const SEND_MESSAGE = 'react-app/dialogs/SEND-MESSAGE'

let initialState = {
    dialogs: [
        { id: 1, name: 'Aleksandr' },
        { id: 2, name: 'Ivan' },
        { id: 3, name: 'Aleks' },
        { id: 4, name: 'Viktor' },
        { id: 5, name: 'Radion' },
        { id: 6, name: 'Senya' }
    ],
    messages: [
        { id: 1, message: 'Hi, man' },
        { id: 2, message: 'Hi, how are you' },
        { id: 3, message: 'It is fine' },
        { id: 4, message: 'Yo' },
        { id: 5, message: 'Yo' },
        { id: 6, message: 'Yo' }
    ]
}

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE: {
            let body = action.newMessageBody
            return {
                ...state,
                messages: [...state.messages, { id: 7, message: body }]
            }
        }
        default:
            return state
    }
}

export const sendMessageCreator = (newMessageBody) => ({ type: SEND_MESSAGE, newMessageBody })

export default dialogsReducer